const checkLength = (string: string) => {
  return string.length >= 12
}

const containsNumbers = (string: string) => {
  const regex = /\d/g
  return regex.test(string)
}

const containsUpperLower = (string: string) => {
  const regex = /(?=.*[a-z])(?=.*[A-Z])/g
  return regex.test(string)
}

const containsSpecial = (string: string) => {
  const chars = " !\"#$%&'()*+,-./:;<=>?@[\\]^_`{|}~".split("")
  const regex = new RegExp("[" + chars.join("\\") + "]", "g")
  return regex.test(string)
}

export { checkLength, containsNumbers, containsUpperLower, containsSpecial }
